.nav-container {
  flex-direction: column;
  background-color: #5a5a6f;
  width: 100%;
  display: flex;
  align-items: center;
  font-family: 'Noto Sans JP', sans-serif;
  color: #fff;
}

.sub-nav-container .categories {
  width: inherit;
  max-width: 1050px;
}

.sections {
  display: flex;
  width: 100%;
}

.logo {
  font-size: 1.4em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 .7em;
  cursor: pointer;
  span:first-child {
    font-size: 1em;
    margin-right: .3em;
  }
}

.logo span:nth-child(2) {
  color: #00e7ff;
  font-size: .8em;
}

.logo:hover {
  background-color: #77AF7A;
  span:nth-child(2) {
    color: #3b34b3;
  }
}

.logo a {
  color: #fff;
  text-decoration: none;
  font-weight: 300;
  margin: 0;
}

.nav-elements {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-list {
  display: flex;
  width: 100%;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-elements a {
  display: block;
  padding: .7em;
  text-decoration: none;
  color: #fff;
}

.list:hover {
  background-color: #77AF7A;
}

.nav-link span:first-child {
  font-size: 1.2em;
  margin-right: .4em;
}

.search-box {
  display: flex;
  margin-right: 1em;
}

.search-box form {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
}

.search-box input {
  display: flex;
  height: 2.4em;
  width: 70%;
  border-radius: .3em;
  border: none;
  align-items: center;
}

.search-box input:focus {
  outline: 0;
}

.search-box button {
  display: flex;
  font-size: 0.8em;
  color: #FFF;
  cursor: pointer;
  height: 100%;
  border: none;
  background-color: transparent;
  align-items: center;
  margin-right: .4em;
}

.search-box button:hover {
  background-color: #77AF7A;
}

.search-box button:focus {
  outline: 0;
}

.showHideBtn {
  display: flex;
  border: none;
  background: transparent;
  color: #fff;
  padding: 10px;
  cursor: pointer;
  &:hover {
    background-color: #77AF7A;
  }
  &:focus {
    outline: none;
  }
}

.categories {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.categories ul {
  width: 100%;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
  a {
    font-size: .8em;
    display: block;
    color: #fff;
    text-decoration: none;
    padding: 1em;
  }
  a:hover {
    background-color: #77AF7A;
  }
}

@media (max-width: 1050px) {
  .nav-link span:nth-child(2) {
    display: none;
  }
  .search-box {
  }
  .search-box form {
    justify-content: flex-start;
    input {
      width: 100%;
      margin-right: 1em;
    }
  }
  .categories ul {
    width: 65%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;
    a {
      font-size: .8em;
      display: block;
      color: #fff;
      text-decoration: none;
      padding: 1em;
    }
  }
}


@media (max-width: 650px) {
  .sections {
    flex-direction: column;
    align-items: center;
  }
  .logo a {
    color: #fff;
    text-decoration: none;
    font-weight: 300;
    margin: 0;
    padding: .2em
  }
  .nav-elements {
    margin-left: 0;
  }

  .nav-link span:nth-child(2) {
    display: none;
  }
  .nav-link span:first-child {
    margin: 0;
  }
  .search-box {
    padding: .7em;
    margin-right: 0;
  }
  .search-box form {
    justify-content: flex-start;
    input {
      width: auto;
      margin-right: 0;
    }
    button {
      padding: .7em;
    }
  }

  .categories ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;
    a {
      font-size: .8em;
      display: block;
      color: #fff;
      text-decoration: none;
      padding: 1em;
    }
  }
}
