.footer-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1050px;
  width: 100%;
}
.footer {
  display: flex;
  flex-direction: column;
  font-size: 14px;
}

.footer ul {
  list-style-type: none;
  padding: 0;
}

.footer ul li:first-child {
  font-size: 20px;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
  a {
    color: black;
  }
}

.footer li {
  margin: 8px 0;
}

.footer a {
  color: #4d5f71;
  text-decoration: none;
}
