.goods-container {
  display: flex;
  width: 100%;
  margin-bottom: 30px;
  img {
    width: 300px;
    height: 300px;
  }
}

.iphone-info-card {
  width: 400px;
  margin: 0 1em;
  a {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: #000;
    // h4 {
    //   color: #E74141;
    //   span {
    //
    //   }
    // }
    // h2 {
    //   width: 177px;
    //   white-space: pre-wrap;
    //   text-align: center;
    // }
    p {
      font-size: 1.11em;
      font-weight: bold;
      text-align: justify;
    }
  }
}

.phones-subcontainer {
  width: 100vw;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
}

.making-margin-right {
  width: 400px;
}

.phone {
  width: 120px;
  // flex: 0 0 auto;
  margin: 0 1em;
  max-width: 180px;
}

.phone-img {
  margin-bottom: 5px;
  a {
    display: block;
      img {
        display: block;
        width: 120px;
        height: 120px;
        background-repeat: no-repeat;
        object-fit: contain;
      }
  }
}

.phone-info {
  width: 120px;
  max-width: 180px;
  p:first-child {
    &::after {
      content: ' â‚½'
    }
  }
  p {
      font-size: .9em;
      color: #E74141;
      font-weight: 600;
      padding: 0;
      margin: 0;
  }
  a {
    display: block;
    height: 4em;
    font-size: .9em;
    text-decoration: none;
    color: #000;
    font-weight: 600;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    &:hover {
      color: blue;
    }
  }

  button {
    width: 120px;
    margin-top: 5px;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: .9em;
    display: block;
    height: 45px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    color: #fff;
    background-color: #3366ff;
    &:hover {
      background-color: #6a88e2;
    }
  }
}

@media (max-width: 1050px) {
  .iphone-info-card {
    display: none;
  }
}

@media (min-width: 650px) {
  .phones-subcontainer {
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .making-margin-right {
    width: auto;
  }
  .phone {
    width: 140px;
  }
  .phone-img {
    a {
        img {
          width: 140px;
          height: 140px;
        }
    }
  }
  .phone-info {
    width: 140px;
    p {
        // font-size: 1.1em;
    }
    a {
      font-size: 1.1em;
      height: auto;
    }

    button {
      width: 140px;
    }
  }
}
